
<script>
import Schema from "../../CRM/PriceBook/PriceBookSchema";
import PriceBookDataService from "../../../services/DataService";
import viewlayout from '../../../components/Common/Modal/view-layout.vue';
import searchfilter from '../../../components/Common/Modal/search-filter.vue';
export default {
    components: {
        viewlayout,
        searchfilter
    },
    data() {
        return {
            appliedFilter:[],
            listheaderbuttons: Schema.buttons,
            PriceBookData: null,
            Headers: null,
            userid: null,
            usertype: null,
            PageNumber: 1,
            PageSize: 10,
            SortBy: '',
            SortExp: '',
            noRecord: false,
            TotalRecords: 0,
            StatusListForListing: [],
            souceID: 0,
            showFilter: false,
            showAddEventModal: false,
            ShowManageView: false,
            searchCondition: '',
            isLoading: false,
            priceBookName: '',
            IsAnySearch: '',
            BookName: '',
            srchcondition: '',  
            leftSearchFields: Schema.leftSearchFields,
            privilegeParams: null,
            Edit:null,
            LegendArray: [],
            Headers: [{
                "COLUMN_NAME": "price_book_name",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Price Book Name",
                "DISPLAY_ORDER": 1
            },
            {
                "COLUMN_NAME": "status_id",
                "DATA_TYPE": "select",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "STATUS_NAME",
                "DISPLAY_ORDER": 2,
            },
            {
                "COLUMN_NAME": "effective_start_date",
                "DATA_TYPE": "date",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Effective From",
                "DISPLAY_ORDER ": 3,
            },
            {

                "COLUMN_NAME": "effective_end_date",
                "DATA_TYPE": "date",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "effective_end_date",
                "DISPLAY_ORDER": 4,

            },
            {
                "COLUMN_NAME": "price_book_owner",
                "DATA_TYPE": "select",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Price Book Owner",
                "DISPLAY_ORDER": 5,
            },
            {
                "COLUMN_NAME": "price_book_model",
                "DATA_TYPE": "select",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Price Book Model",
                "DISPLAY_ORDER": 6,
            },
            {
                "COLUMN_NAME": "View",
                "DATA_TYPE": "",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "View",
                "DISPLAY_ORDER": 7,
            },
            {
                "COLUMN_NAME": "Edit",
                "DATA_TYPE": "",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Edit",
                "DISPLAY_ORDER": 8,
            },
            ],
            ViewList:[],
            ViewListId:''
        }
    },
    created: function () {
        this.userid = this.GetUserInfo.ID;
        this.usertype = this.GetUserInfo.UserType;
        this.privilegeParams = `controller=CRM&action=`;
        var VueObj = this;
        VueObj.FetchData();
        VueObj.GetDdlRecords_ViewList();
        VueObj.getStatuslistForListing('CRM', 'en');
        VueObj.LegendArray = [
            {
                 class: 'bg-primary colorboxsquare',
                 text: (VueObj.$t('CloseLeadLegend', ['Price Books']))
             }
         ]
    },
    methods: {
        GetDdlRecords_ViewList: async function () {
          //  debugger
            var vm = this;
            vm.ViewList=[];
            var url = `moduleName=CRM&subModuleCode=CRM_PRICE_BOOKS&viewId=0&pageSize=100&pageNum=1&sortBy=&sortExp=`;
            await PriceBookDataService.GetViewList(url).then(function (response) {   
                debugger             
                if(response.data && typeof(response.data.DATA) != 'undefined'){
                    vm.ViewList = response.data.DATA;
                }
                var chkVACTIVE = 0;
                vm.ViewListId = "0";
                if (vm.ViewList == null || vm.ViewList == "") {
                   
                }
                else {
                    vm.ViewList.forEach(function (item) {

                        if (item.IS_SLECTED_USER_ID > 0) {
                            chkVACTIVE = chkVACTIVE + 1;
                            vm.ViewListId = item.VIEW_ID;
                        }
                    });
                }
                var indx = 0;
                if (chkVACTIVE > 0) {
                    vm.ViewList.splice(indx, 0, { "SUB_MODULE_NAME": "", "SUB_MODULE_CODE": "", "VIEW_NAME": "Default View", "VIEW_ID": 0, "IS_APPLICABLE_TO_ALL": 1, "MODULE_NAME": "", "CREATED_AT": "", "IS_SLECTED_USER_ID": 0, "CAN_EDIT": 0, "ROWNUMBER": 0, "VIEW_TYPE": "", "USER_LIST": "" });
                }
                else {
                    if (vm.ViewList == null || vm.ViewList == "") {

                        vm.ViewList.push({ "SUB_MODULE_NAME": "", "SUB_MODULE_CODE": "", "VIEW_NAME": "Default View", "VIEW_ID": 0, "IS_APPLICABLE_TO_ALL": 1, "MODULE_NAME": "", "CREATED_AT": "", "IS_SLECTED_USER_ID": 1, "CAN_EDIT": 0, "ROWNUMBER": 0, "VIEW_TYPE": "", "USER_LIST": "" });
                    }
                    else {
                        vm.ViewList.splice(indx, 0, { "SUB_MODULE_NAME": "", "SUB_MODULE_CODE": "", "VIEW_NAME": "Default View", "VIEW_ID": 0, "IS_APPLICABLE_TO_ALL": 1, "MODULE_NAME": "", "CREATED_AT": "", "IS_SLECTED_USER_ID": 1, "CAN_EDIT": 0, "ROWNUMBER": 0, "VIEW_TYPE": "", "USER_LIST": "" });

                    }
                }
                setTimeout(() => {
                    if( vm.ViewListId > 0){
                        vm.$refs.tgList.$refs.tgListHeaderButtons.ViewListId = vm.ViewListId;    
                    }else{
                        vm.$refs.tgList.$refs.tgListHeaderButtons.ViewListId = "0";
                    }
                }, 1000);  
                                
            });           
        }, 
        DefaultView: function () {  
           // debugger                                 
            var vm = this;
            var obj = {
                ModuleName: 'CRM',
                SubmoduleCode: 'CRM_PRICE_BOOKS'
            };            
            var objectPostString = JSON.stringify(obj);
            PriceBookDataService.ClearView(objectPostString).then(function (response) {
                if (response.data != null) {
                    if (response.data.code == "1" || response.data.Code == "1") {
                        vm.ShowAlert(vm.$t("ClearSuccess", [vm.$t("DefaultView")]), 'success', true, vm.$t('Alert'));
                        //vm.$emit('RefreshManageView');
                        vm.FetchData();
                    }
                    else {
                        vm.ShowAlert(vm.$t("ErrorWhileProcessingRequest"), 'failure', true, vm.$t('Alert'));
                    }
                }
                else {
                    vm.ShowAlert(vm.$t("ErrorWhileProcessingRequest"), 'failure', true, vm.$t('Alert'));
                }
            });
        },
        OnApplyView(viewId) {  
           // debugger         
            var vm = this;
            if (viewId == '0') {
                vm.DefaultView()
            }
            else {
                var obj = {
                    ViewId: viewId,
                    ModuleName: 'CRM',
                    SubmoduleCode: 'CRM_PRICE_BOOKS'
                };
                var objectPostString = JSON.stringify(obj);                     
                PriceBookDataService.ApplyView(objectPostString).then(function (response) {
                    if (response.data != null) {
                        if (response.data.code == "1" || response.data.Code == "1") {
                            vm.ShowAlert(vm.$t("ApplySuccess", [vm.$t("View")]), 'success', true, vm.$t('Alert'));
                            vm.$emit('RefreshManageView');
                            vm.FetchData();
                        }
                        else {
                            vm.ShowAlert(vm.$t("AddedError", [vm.$t("View")]), 'failure', true, vm.$t('Alert'));
                        }
                    }
                    else {
                        vm.ShowAlert(vm.$t("AddedError", [vm.$t("View")]), 'failure', true, vm.$t('Alert'));
                    }
                });

            }
        },
        renderActions(){

        },
        cancelOrRefresh: function () {
            this.BookName = '';
            this.priceBookName = '';
            this.searchCondition = '';
            this.srchcondition = '';
            this.IsAnySearch = false;
            this.FetchData();
        },

        leftCommonSearch: function (value) {
            this.PageNumber = value.PageNumber;
            if (typeof value.searchCondition != 'undefined') {
                this.searchCondition = value.searchCondition;
            }
            else {
                this.searchCondition = "";
            }
            this.FetchData();
        },

        Searchdata: function (event) {
            this.PageNumber = 1;
            this.GroupStart = this.PageNumber;
            this.GroupEnd = this.PageNumber + 2;
            this.IsAnySearch = false;
            if ($.trim(this.priceBookName) != "") {
                this.IsAnySearch = true;
                this.srchcondition += ($.trim(this.srchcondition) != "") ? " AND" : "";
                this.srchcondition += " del.price_book_name LIKE '%25" + this.priceBookName + "%25'";
            }
            this.FetchData();
        },
        sortdata: function (s) {
            var SortDir = "sort tb_headerSortDown";
            this.SortBy = s;
            if (s === this.SortBy) {
                if (this.SortExp == "") {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
                else if (this.SortExp == "ASC") {
                    this.SortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                }
                else {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
            }
            this.FetchData();
            $("#" + s + '  span:first-child').attr("class", SortDir);
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },
        commonDeleteData: function (event) {
            var vm = this;
      PriceBookDataService.CheckPrivilege(vm.privilegeParams + 'DeletePriceBook')
                .then(function (response) {
                    if (response.data) {
                        var val = "";
                        $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                            if (val.length > 0)
                                val += ',';
                            val += $(this).val();
                        });
                        if (val.length > 0) {
                            vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("PriceBook")), function () {
                                var postJSON = {
                                    ids: val,
                                    moduleName: "CRM",
                                    subModuleCode: "CRM_PRICE_BOOKS"
                                };
                                var objectPostString = JSON.stringify(postJSON);
                                PriceBookDataService.CommonDeleteData(objectPostString).then(function (response) {
                                    var box = "";
                                    if (response.status == 200) {
                                        $("[id^='chkAll']").prop("checked", false).removeAttr('checked');
                                 $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                                        $("#DeleteMultiple").addClass("disabled");
                                        box = vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('PriceBook')]), "Success", true, vm.$t("Alert"));
                                        vm.PageNumber = parseInt(vm.PageNumber) - 1;
                                        if (vm.PageNumber <= 0) {
                                            vm.PageNumber = 1;
                                        }
                                        vm.FetchData();

                                    }
                                    else {
                                        box = vm.ShowAlert(vm.$t('DeletedError', [vm.$t('PriceBook')]), "failure", true, vm.$t("Alert"));

                                    }
                                    setTimeout(function () {
                                        box.modal('hide');
                                    }, 2000);

                                },);
                            });
                        }
                        else {
                            vm.ShowAlert(vm.$t("Selectanyrecordtodelete"));
                        }
                    }
                    else {
                        vm.ShowAlert('You are not authorized', "failure", true, "Alert");
                    }
    });
        },
        ManageView: function () {
            this.ShowManageView = true;
            setTimeout(function () {
                $("#modalManageView").modal('show');
                setTimeout(function () {
                    SetBootstraptoolTip(); 0
                }, 200);
            }, 1000);
        },
        FilterData: function (pageNum, pageSize, sortBy, sortExp, searchcondition, filterItemData) {
            this.pageNum = pageNum;
            this.pageSize = pageSize;
            this.sortBy = sortBy;
            this.sortExp = sortExp;
            this.searchCondition = searchcondition;
            if (typeof filterItemData != null)
                this.appliedFilter.push(filterItemData);
            this.FetchData();
        },
        openFilterPopUp() {
            this.showFilter = true;
        },
        getStatuslistForListing: function (StatusType, lang) {
            var vm = this;
            var url = "langCode=" + lang + "&statusType=" + StatusType + "&hasglobal=true";
             
            PriceBookDataService.statusdropdown(url).then(function (response) {
                if (response.data.status == "Success") {
                    var json = response.data.result;
                    vm.StatusListForListing = json.data;
                }
            });
        },
        commonHeaderButtonClick: function (event) {
            switch (event.callbackfunction) {
                case "ManageView":
                    this.ManageView();
                    break;
                case "openFilterPopUp":
                    this.openFilterPopUp();
                    break;
                case "downloadExceldata":
                    this.FetchData(true);
                    break;
                case "commonDeleteData":
                    this.commonDeleteData();
                    break;
                case "removeFilter":
                    var ths = this;
                        this.searchCondition = '';
                        const removeFilterindex= ths.listheaderbuttons.findIndex(object => {
                        return object.callbackfunction === 'removeFilter';});
                        ths.listheaderbuttons[removeFilterindex].isvisible=false;
                        const openFilterPopUpindex= ths.listheaderbuttons.findIndex(object => {
                        return object.callbackfunction === 'openFilterPopUp';});
                        ths.listheaderbuttons[openFilterPopUpindex].iconClass='fa fa-filter';
                         try {
                            ths.appliedFilter = [];
                         } catch (e) { };
                         ths.FetchData();
                        break; 
                        case "setTourGuideSteps":
                    this.setTourGuideSteps(0);
                        break;
            }
        },
        getRequestParams: function (pageNum, pageSize, sortBy, sortExp, isPaged, pricebookname,searchCondition) {
            let params = {};
            if (pageNum) { params["pageNum"] = pageNum; }
            if (pricebookname) { params["pricebookname"] = pricebookname; }
            if (searchCondition) { params["searchCondition"] = searchCondition; }
            if (isPaged) { params["isPaged"] = isPaged; }
            if (sortBy) { params["sortBy"] = sortBy; }
            if (sortExp) { params["sortExp"] = sortExp; }
            if (pageSize) { params["pageSize"] = pageSize; }            
            return params;
        },
        clickEventOfColumn:function(row, column){
                if(column.COLUMN_NAME == 'price_book_name'){
                    this.$router.push({ path: '/CRM/ViewPriceBookDetails/'+row.price_book_id+'/CRM/CRM_PRICE_BOOKS' });
                }
        },
        FetchData: function (downloadExceldata) {
            const params = this.getRequestParams(this.pageNumber, this.pageSize, this.sortBy, this.sortExp, this.isPaged, this.pricebookname,this.searchcondition);
            var VueObj = this;
            VueObj.isLoading = true;
            if(downloadExceldata){
                params.pageSize = VueObj.TotalRecords;
            }else{ 
                params.pageSize = VueObj.PageSize; 
            }
            
            params.pageNumber = VueObj.PageNumber;
            params.searchCondition = VueObj.searchCondition;
            params.pricebookname = VueObj.leftSearchFields[0].value;
            if (VueObj.PageNumber) { params.pageNum = VueObj.PageNumber; }
            else { VueObj.PageNumber = params.pageNum = 1; }
            if (typeof VueObj.isPaged != 'undefined') { params.isPaged = VueObj.isPaged; }
            else { VueObj.isPaged = params.isPaged = 1; }
            params.isPaged = VueObj.isPaged;
            params.sortBy = VueObj.SortBy;
            params.sortExp = VueObj.SortExp;
            var srchCondition = "";
            if (!VueObj.isBlank(VueObj.searchCondition)) { srchCondition = srchCondition + VueObj.searchCondition; }
            if (!VueObj.isBlank(VueObj.conditionForInstantSearch)) { srchCondition = srchCondition + ((!VueObj.isBlank(VueObj.searchCondition)) ? (" AND " + VueObj.conditionForInstantSearch) : VueObj.conditionForInstantSearch); }
            if (srchCondition != "") {
                params.searchCondition = srchCondition; 
                if(VueObj.appliedFilter.length>0){
                const removeFilterindex= VueObj.listheaderbuttons.findIndex(object => {
                return object.callbackfunction === 'removeFilter';
                });          
                VueObj.listheaderbuttons[removeFilterindex].isvisible=true;      
                const openFilterPopUpindex= VueObj.listheaderbuttons.findIndex(object => {
                return object.callbackfunction === 'openFilterPopUp';
                });
                VueObj.listheaderbuttons[openFilterPopUpindex].iconClass='fa fa-filter text-success';
                }  
            }
            else{
                if(VueObj.appliedFilter.length==0){
                const removeFilterindex= VueObj.listheaderbuttons.findIndex(object => {
                return object.callbackfunction === 'removeFilter';
                });          
                VueObj.listheaderbuttons[removeFilterindex].isvisible=false;      
                const openFilterPopUpindex= VueObj.listheaderbuttons.findIndex(object => {
                return object.callbackfunction === 'openFilterPopUp';
                });
                VueObj.listheaderbuttons[openFilterPopUpindex].iconClass='fa fa-filter';
            }
          }
            if (downloadExceldata) {
                params.isPaged = 0;
                PriceBookDataService.PriceBookListing(params)
                    .then(function (response) {
                        if (response.data != "" && response.data != null) {
                            VueObj.JSONToCSVConvertor(response.data, "PriceBook-Detail", true);
                        }
                        else {
                            VueObj.ModelnoRecord = true;
                        }
                        VueObj.isLoading = false;
                    });
            } else {
                PriceBookDataService.PriceBookListing(params).then(function (response) {
                    if (response.data != null) {                      
                    VueObj.Headers.forEach((column) => {
                    column.settings = null;
                    if(column.COLUMN_NAME == 'price_book_name'){
                        column.settings = {
                            clickEvent: VueObj.clickEventOfColumn,
                            formatter: VueObj.columnDataFormatter
                        }
                    }
                    if(column.COLUMN_NAME == 'status_id'){
                        column.settings = {
                        isInSlot : true
                        }
                    }
                    if(column.COLUMN_NAME == 'View'){
                        column.settings = {
                            isInSlot : true
                        }
                    }
                    if(column.COLUMN_NAME == 'Edit'){
                        column.settings = {
                        isInSlot : true
                        }
                    }
                });
                        if (response.data.data.length > 0) {
                            VueObj.PriceBookData = response.data.data;
                            VueObj.PriceBookData.forEach((row)=>{
                                row.isCheckBoxDisabled=false;
                                row.additionalClass='';
                                if(row.status_id=='Closed'){
                                    row.isCheckBoxDisabled=true;
                                    row.additionalClass='medium-bar';
                                }
                            row.rowDetailsUrl='';
                            if(row.rowDetailsUrl=='')
                            {
                                row.rowDetailsUrl='/CRM/ViewPriceBookDetails/'+row.price_book_id+'/CRM/CRM_PRICE_BOOKS';
                            }
                            });
                            VueObj.TotalRecords = response.data.data[0].totalrecords;
                            VueObj.TotalPages = Math.ceil(VueObj.TotalRecords / VueObj.PageSize);
                            VueObj.CurrentPage = VueObj.PageNumber;
                            $("[id^='chkAll']").prop("checked", false).removeAttr('checked');
                            $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                            $('#DeleteMultiple').addClass('disabled');
                            VueObj.GetDdlRecords_ViewList();
                            VueObj.noRecord = false;
                        } else {
                            VueObj.noRecord = true;
                            VueObj.PriceBookData = [];
                            VueObj.TotalRecords = 0
                        }
                    }
                    else {
                        VueObj.noRecord = true;
                        VueObj.PriceBookData = [];
                        VueObj.Headers = [];
                        VueObj.TotalRecords = 0
                    }
                    setTimeout(function(){
                                VueObj.ResponsiveDataTable('tablelistingdata')
                            }, 500); 
                    VueObj.isLoading = false;                             
                });
            }
        }
    }
}
</script>

<template>
    <section>
        <loader :is-visible="isLoading"></loader>
        <tg-list 
            :IsShowAction="false" 
            :showCheckBox="true"
            ModuleName="CRM"
            v-on:ApplyView="OnApplyView" :ViewList="ViewList"
                ref="tgList" 
            SubModuleCode="CRM_PRICE_BOOKS" 
            IdentityColumn="price_book_id"
            :HeaderText="$t('PRICE_BOOKS')" 
            :ListData="PriceBookData" 
            :HeaderData="Headers"
            :RenderRowActionMethod="renderActions" 
            :HeaderButtons="listheaderbuttons" 
            :TotalRecords="TotalRecords"
            :SearchFields="leftSearchFields" 
            :LegendArray="LegendArray"
            :ListDataCallBackFunction="FetchData"
            :IsFilterApplied="searchCondition?true:false"
            :SortExp="SortExp"
            :SortBy="SortBy"
            :NorecordfoundText="$t('NoRecordfound')"
            :CurrentPageNumber="PageNumber"
            v-on:LeftsearchButtonClick="leftCommonSearch" 
            v-on:PagerButtonClick="pagerMethod"
            v-on:HeaderButtonClick="commonHeaderButtonClick" 
            v-on:SortdataButtonClick="sortdata">
            <template v-slot:slotdata="{data}">
            <template v-if="data.column.COLUMN_NAME == 'View'">               
                <td class="text-center single-action tour_viewitem">     
                    <RouterLink
                        :title="$t('ViewPriceBook')" 
                        :to="'/CRM/ViewPriceBookDetails/'+data.row.price_book_id+'/CRM/CRM_PRICE_BOOKS'" 
                        :typetitle="$t('View')" typefor="Edit" v-bind:value="data.row.price_book_id" style="color: dodgerblue">
                        <em class="fa fa-eye text-info action-icon"></em>
                    </RouterLink>
                </td>
            </template>
            <template v-if="data.column.COLUMN_NAME == 'Edit'">
                <td class="text-center single-action tour_viewitem">
                    <RouterLink
                        :title="$t('EditPriceBook')" 
                        v-bind:class="{'disabled':data.row.status_id=='Closed'}" 
                        :to="'/CRM/ManagePriceBook/'+data.row.price_book_id+'/CRM/CRM_PRICE_BOOKS'"                            
                        :typetitle="$t('Edit')" 
                        :typefor="Edit" 
                        v-bind:value="data.row.price_book_id">
                        <em class="fa fa-pencil text-success action-icon action-icon1" v-bind:class="{'disabled':data.row.status_id=='Closed'}"></em>
                    </RouterLink>
                </td>
            </template>
            <template v-if="data.column.COLUMN_NAME == 'status_id'">
                <listing-status :isfor="'CRM_PRICE_BOOKS'" v-if="isCRMShowStausDrop(data.row.status_id, 'CRM_PRICE_BOOKS')" ref="updatestatus" v-bind:dataid="data.row.price_book_id" data-controller="Crm" v-bind:statuslist="StatusListForListing" v-bind:item="data" data-action="index" v-bind:dataselected="data.row.status_id" v-bind:callbackfunction="FetchData" v-bind:isselectedvaluestring="true">
                </listing-status>
                <a class="inactive-disabled" v-if="!isCRMShowStausDrop(data.row.status_id, 'CRM_PRICE_BOOKS')">
                    <span v-bind:class="getStatusClass(data.row.status_id, 'status_id')">
                        {{data.row[data.column.COLUMN_NAME]}}
                    </span>
                </a>
            </template>
        </template>
        </tg-list>
        <searchfilter 
                v-if="showFilter" 
                submodulecode="CRM_PRICE_BOOKS" 
                modulename="CRM" 
                :userid="userid" 
                :companyid="CompanyId" 
                @close="showFilter = false" 
                v-bind:callbackfunction="FetchData"
                @FilterData="FilterData">
                <h5 slot="header">{{$t('Search')}}</h5>
            </searchfilter> 

        <viewlayout v-if="ShowManageView" v-show="ShowManageView" submodulecode="CRM_PRICE_BOOKS" modulename="CRM"
            @close="ShowManageView = false" v-bind:callbackfunction="FetchData" v-bind:commonapiurl="commonAPIUrl"
            :usertype="usertype">
            <h5 slot="header">{{$t('Search')}}</h5>
        </viewlayout>

    </section>
</template>
